.js30-content .items {
  height: 700px;
  padding: 70px;
  width: 100%;
  overflow-x: scroll;
  overflow-y: hidden;
  white-space: nowrap;
  user-select: none;
  cursor: grab;
  transition: all 0.2s;
  transform: scale(0.98);
  will-change: transform;
  position: relative;
  font-size: 0;
  perspective: 800px;
}
.js30-content .items::-webkit-scrollbar {
  display: none;
}
.js30-content .items.active {
  cursor: grabbing;
  cursor: -webkit-grabbing;
  transform: scale(1);
}
.js30-content .item {
  width: 200px;
  height: calc(100% - 40px);
  font-family: "PS900";
  display: inline-flex;
  align-items: center;
  justify-content: center;
  font-size: 70px;
  color: rgba(0, 0, 0, 0.15);
  box-shadow: inset 0 0 0 10px rgba(0, 0, 0, 0.15);
}
.js30-content .item:nth-child(odd) {
  background: rgba(var(--accent-r), var(--accent-g), var(--accent-b), 0.84);
}
.js30-content .item:nth-child(even) {
  background: rgba(var(--accent-r), var(--accent-g), var(--accent-b), 0.42);
}
.js30-content .item:nth-child(even) {
  transform: scaleX(1.31) rotateY(40deg);
}
.js30-content .item:nth-child(odd) {
  transform: scaleX(1.31) rotateY(-40deg);
}